.main-div {
  background-color: aqua;
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.center-div {
  width: 300px;
  height: 80vh;
  background-color: #f4f4f4;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
}

h1 {
  color: white;
  background-color: aquamarine;
  padding: 5px 0;
  margin-bottom: 10px;
}

input {
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid aquamarine;
}

input:focus {
  background: none;
  outline: none;
}

.addBtn {
  min-height: 40px;
  width: 40px;
  border-radius: 50%;
  background-color: aquamarine;
  margin-left: 10px;
  border: none;
  outline: none;
  color: white;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
}

.addBtn:hover {
  background-color: lime;
}

.ol {
  list-style: none;
  text-align: left;
  overflow: auto;
  height: 300px;
}
