.cross {
  transform: rotate(45deg);
  border: none;
  outline: none;
  width: 30px;
  height: 30px;
  background-color: aquamarine;
  border-radius: 50%;
  font-size: 22px;
  font-weight: bold;
  color: white;
  cursor: pointer;
  margin-right: 10px;
}

.cross:hover {
  background-color: red;
}

.li {
  margin-bottom: 5px;
  font-size: 17px;
  padding-bottom: 3px;
}
